.search-field {
    height: inherit;
    line-height: normal;
}

.search-field input.ant-input,
.search-field .ant-btn {
    font-size: 25px;
}

.search-field .ant-input-affix-wrapper,
.search-field .ant-input-affix-wrapper:focus,
.search-field .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
    outline: none;
    border: none;
    box-shadow: none;
    background: transparent;
}

.search-field--white .ant-space-compact {
    background: #fff;
    border-radius: 6px;
}

.search-field .ant-btn {
    height: auto;
}

.search-icon {
    font-size: 20px;
    color: #adb5bd;
}

.search-field .ant-input {
    background: transparent;
}
